.tempC {
  position: absolute;
  top: 1%;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0.68;
  font-family: AvenirNextCondensed-UltraLight;
  font-size: 18vw;
  color: #FFFFFF;
  letter-spacing: -7px;
  text-shadow: 0 2px 10px rgba(0,0,0,0.50);
  margin: 0;
}

.break_1 {
  position: absolute;
  top: 68%;
  left: 50%;
  transform: translateX(-50%);
  width: 85%;
  border-bottom: 1px  solid #ACACAC;
  box-shadow: 0 1px 1px 0 rgba(118,118,118,0.50);
}

@media only screen and (orientation: portrait) {

  .tempC {
    position: absolute;
    top: auto;
    bottom: -10%;
    left: 50%;
    transform: translateX(-50%);
    padding: 0;
    opacity: 0.68;
    font-family: AvenirNextCondensed-UltraLight;
    font-size: 14vh;
    color: #000000;
    letter-spacing: 0;
    text-shadow: 0 2px 10px rgba(0,0,0,0.50);
  }

  .break_1 {
    display: none;
  }
}
