@font-face {
  font-family: AvenirNextCondensed-Regular;
  src: url(/warsaw-weather/static/media/AvenirNextCondensed-Regular.3f326f52.ttf);
}

@font-face {
  font-family: AvenirNextCondensed-UltraLight;
  src: url(/warsaw-weather/static/media/AvenirNextCondensed-UltraLight.0f21db47.ttf);
}

html {
  min-height: 100%;
  overflow: hidden;
}

body {
  min-width: 100%;
}

.background {
  position: absolute;
  top:0;
  left: 0;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: 100vw 100vh;
  background-position: bottom;
}

.app {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  width: 100vw;
  height: 56.25vw;
  max-height: 100%;
  max-width: 177.5vh;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: bottom;
}

.container {
  position: absolute;
  z-index: +1;
  bottom: 1.5%;
  right: 1.5%;
  width: 41%;
  height: 60%;
}

.nav_box_1, .nav_box_2, .nav_box_3 {
  position: absolute;
  top: 3.1%;
  left: 1%;
  height: 6.5%;
  opacity: 0.87;
  background: #FFFDFD;
  border-radius: 24px;
  z-index: -10;
}

.nav_box_1 {
  width: 16%;
}

.nav_box_2 {
  width: 14%;
}

.nav_box_3 {
  width: 19%;
}

.box_1 {
  position: absolute;
  bottom: 1.5%;
  right: 1.5%;
  width: 41%;
  height: 60%;
  opacity: 0.32;
  background: #FFFDFD;
  box-shadow: 2px 2px 4px 0 rgba(0,0,0,0.50);
  border-radius: 1.5vw;
}

.box_2 {
  position: absolute;
  bottom: 1.5%;
  left: 1%;
  width: 54%;
  height: 19%;
  opacity: 0.65;
  background: #FFFDFD;
  box-shadow: 2px 2px 4px 0 rgba(0,0,0,0.50);
  border-radius: 1.5vw;
}

@media only screen and (orientation: portrait) {

  .app {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    width: 56.25vh;
    height: 100%;
    padding-top: 0;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: bottom;
    box-shadow: 1px 1px 16px 0 rgba(123,123,123,0.50),
                -1px -1px 16px 0 rgba(123,123,123,0.50);
  }

  .container {
    position: absolute;
    z-index: +1;
    top: 18%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: auto;
    right: 1.5%;
    width: 50%;
    height: 25%;
  }

  .box_1 {
    position: absolute;
    bottom: auto;
    top: 18%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 50%;
    height: 25%;
    opacity: 0.8;
    background: #FFFDFD;
    box-shadow: 2px 2px 4px 0 rgba(0,0,0,0.50);
    border-radius: 1.5vw;
  }

  .box_2 {
    position: absolute;
    bottom: 1%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 95%;
    height: 12%;
    opacity: 0.65;
    background: #FFFDFD;
    box-shadow: 2px 2px 4px 0 rgba(0,0,0,0.50);
    border-radius: 1.5vw;
  }

  .nav_box_1, .nav_box_2, .nav_box_3 {
    display: none;
  }

}

.date_time {
  position: absolute;
  bottom: 10%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity: 0.69;
  font-family: AvenirNextCondensed-Regular;
  font-size: 3.5vw;
  color: #FFFFFF;
  text-shadow: 0 2px 10px rgba(0,0,0,0.50);
  margin: 0 -30% 0 0;
}

@media only screen and (orientation: portrait) {
  .date_time {
    display: none;
  }
}

.tempC {
  position: absolute;
  top: 1%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity: 0.68;
  font-family: AvenirNextCondensed-UltraLight;
  font-size: 18vw;
  color: #FFFFFF;
  letter-spacing: -7px;
  text-shadow: 0 2px 10px rgba(0,0,0,0.50);
  margin: 0;
}

.break_1 {
  position: absolute;
  top: 68%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 85%;
  border-bottom: 1px  solid #ACACAC;
  box-shadow: 0 1px 1px 0 rgba(118,118,118,0.50);
}

@media only screen and (orientation: portrait) {

  .tempC {
    position: absolute;
    top: auto;
    bottom: -10%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    padding: 0;
    opacity: 0.68;
    font-family: AvenirNextCondensed-UltraLight;
    font-size: 14vh;
    color: #000000;
    letter-spacing: 0;
    text-shadow: 0 2px 10px rgba(0,0,0,0.50);
  }

  .break_1 {
    display: none;
  }
}

.btn {
  position: absolute;
  top: 4.5%;
  left: 2.2%;
  width: 2.5%;
}

.btn_img {
  position: absolute;
  top: 0;
  max-width: 100%;
  padding-bottom: 50%;
}

.dropdown {
  position: absolute;
  top: 10%;
  left: 1%;
  padding: 0;
  margin: 0;
  opacity: 0.87;
  background: #FFFDFD;
  border-radius: 12px;
  cursor: pointer;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 10%;
  min-width: 4.5rem;
}

.dropdown li {
  padding: 10%;
  font-family: Arial;
  font-size: 1.2vw;
  color: #000000;
  text-decoration: none;
  list-style-type: none;
}

.dropdown li:nth-of-type(1):hover {
  background: #E4DFDF;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.dropdown li:nth-of-type(2):hover {
  background: #E4DFDF;
}

.dropdown li:nth-of-type(3):hover {
  background: #E4DFDF;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}

@media only screen and (orientation: portrait) {
  .btn {
    position: absolute;
    top: 3%;
    left: auto;
    right: 4%;
    width: 9%;
  }

  .btn_img {
    position: absolute;
    top: 0;
    max-width: 100%;
    padding-bottom: 0;
  }

  .dropdown {
    position: absolute;
    top: 2.5%;
    left: auto;
    right: 4%;
    opacity: 1;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    background: #FFFDFD;
    border-radius: 6px;
    cursor: pointer;
    z-index: 100000;
    width: 54%;
    min-width: none;
  }

  .dropdown li {
    padding: 2.5%;
    font-family: Arial;
    font-size: 1.2vw;
    color: #000000;
    text-decoration: none;
  }

  .dropdown li:nth-of-type(1):hover {
    background: #DDDDDD;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border-top-right-radius: 6px;
    border-bottom-right-radius: 6px;
  }

  .dropdown li:nth-of-type(2):hover {
    background: #DDDDDD;
  }

  .dropdown li:nth-of-type(3):hover {
    background: #DDDDDD;
    border-top-left-radius: 6px;
    border-top-right-radius: 0;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 0;
  }
}

.title {
  position: absolute;
  top: 2.6%;
  left: 6%;
  font-family: AvenirNextCondensed-Regular;
  font-size: 3.2vw;
  margin: 0;
  color: #000000;
}

.facade {
  position: absolute;
  top:0;
  left: 0;
  height: 100%;
  margin: 0;
  box-shadow: 4px 0px 8px -2px rgba(0,0,0,0.50);
  z-index: -1000;

}

@media only screen and (orientation: portrait) {

  .facade {
    position: absolute;
    top:30%;
    height: auto;
    width: 100%;
    margin: 0;
    box-shadow: 0px -1px 8px 0px rgba(0,0,0,0.50);
    z-index: -1000;

  }

  .title {
    position: absolute;
    top: 1.5%;
    left: 4%;
    font-family: AvenirNextCondensed-Regular;
    font-size: 5.5vh;
    margin: 0;
    color: #4F4F4F;
    text-shadow: 0 0px 5px rgba(0,0,0,0.20);
  }

}

.wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  position: absolute;
  bottom: 1.5%;
  left: 1%;
  width: 54%;
  height: 19%;
