.date_time {
  position: absolute;
  bottom: 10%;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0.69;
  font-family: AvenirNextCondensed-Regular;
  font-size: 3.5vw;
  color: #FFFFFF;
  text-shadow: 0 2px 10px rgba(0,0,0,0.50);
  margin: 0 -30% 0 0;
}

@media only screen and (orientation: portrait) {
  .date_time {
    display: none;
  }
}
